.about-page{text-align:center;display:flex;align-items:center;width:80%;justify-content:space-around;flex-direction:row;flex-wrap:wrap}.stage-cube-cont{width:500px;display:flex;flex-direction:column;justify-content:center;align-items:center}.skills{width:50%;margin:0 auto;padding:20px;background-color:#e0e0e0;border-radius:8px;box-shadow:0 4px 8px rgba(0,0,0,.1)}.skills p{font-size:1.2rem;color:#333;margin-bottom:20px;text-align:center}.skills ul{list-style-type:none;padding:0}.skills li{margin-bottom:15px;font-size:1rem;color:#555}.skills li strong{color:#007bff;font-size:1.1rem}.skills li::before{content:"âœ”";color:#007bff;margin-right:10px}.cubespinner{flex-wrap:wrap;animation-name:spincube;animation-timing-function:ease-in-out;animation-iteration-count:infinite;animation-duration:12s;transform-style:preserve-3d;transform-origin:100px 100px 0}.cubespinner div{position:absolute;width:200px;height:200px;border:1px solid #ccc;background:rgba(255,255,255,.4);text-align:center;font-size:100px;display:flex;justify-content:center;align-items:center;box-shadow:0 0 20px 0px #ffffe0}.cubespinner .face1{transform:translateZ(100px);color:#dd0031}.cubespinner .face2{transform:rotateY(90deg) translateZ(100px);color:#f06529}.cubespinner .face3{transform:rotateY(90deg) rotateX(90deg) translateZ(100px);color:#28a4d9}.cubespinner .face4{transform:rotateY(180deg) rotateZ(90deg) translateZ(100px);color:#5ed4f4}.cubespinner .face5{transform:rotateY(-90deg) rotateZ(90deg) translateZ(100px);color:#efd81d}.cubespinner .face6{transform:rotateX(-90deg) translateZ(100px);color:#ec4d28}@keyframes spincube{from,to{transform:rotateX(0deg) rotateY(0deg) rotateZ(0deg)}16%{transform:rotateY(-90deg) rotateZ(90deg)}33%{transform:rotateY(-90deg) rotateX(90deg)}50%{transform:rotateY(-180deg) rotateZ(90deg)}66%{transform:rotateY(-270deg) rotateX(90deg)}83%{transform:rotateX(90deg)}}.frontEnd{color:#946b2d}li{color:rgba(255,255,255,.4)}.resumefile a{margin-left:10px;color:#fff;text-shadow:0 0 7px #fff,0 0 10px #fff,0 0 21px #fff}